import { loadStyles } from 'load-themed-styles';

loadStyles('.ms-Check{display:inline-block;cursor:default;line-height:0;vertical-align:top}.ms-Check.is-checked .ms-Check-circle{fill:"[theme:themePrimary, default: #0078d7]";stroke:"[theme:white, default: #ffffff]";stroke-width:1px}.ms-Check.is-checked .ms-Check-check{stroke:"[theme:white, default: #ffffff]"}.ms-Check-circle{fill:"[theme:white, default: #ffffff]";stroke:"[theme:neutralTertiaryAlt, default: #c8c8c8]"}.ms-Check-check{stroke:"[theme:neutralTertiaryAlt, default: #c8c8c8]"}');



